.App {
  text-align: center;
  display: grid;
  grid-template-rows: 15% 65% 20%;
  background-color: #282c34;
  height: 100vh;
  width: 100vw;
}

.App-logo {
  pointer-events: none;
  height: 5vmax;
}

@media (prefers-reduced-motion: no-preference) {
  .react {
    margin-left: -20%;
    animation: App-logo-spin infinite 60s linear;
  }
}

.App-header {
  display: grid;
  grid-template-columns: 4% 5% auto; 
  column-gap: 2%;
  margin-top: 2vmin;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
